<template>
  <!-- ナビ関係を纏めたコンポーネント -->
  <div>
    <nav class="global-nav">
      <!-- ボタンクリックでshowMenuを切り替え -->
      <button class="hamburger" @click="showMenu = !showMenu">
        <HamburgerIcon/> 
      </button>

      <transition name="fade" mode="out-in">
        <!-- showMenuを参照して表示の切り替え -->
        <div class="wrap-router" v-if="showMenu">
          <router-link to="/">Top</router-link>
          <router-link to="/introduction">Introduction</router-link>
          <router-link to="/story">Story</router-link>
          <router-link to="/cast">Cast</router-link>
          <router-link to="/staff">Staff</router-link>
          <router-link to="/comment">Comment</router-link>
          <router-link to="/movie">Movie</router-link>
          <router-link to="/theater">Theater</router-link>
          <router-link to="/news">News</router-link>
        </div>
      </transition>
    </nav>
  </div>
</template>

<script>
import HamburgerIcon from '../atoms/HamburgerIcon.vue'

export default {
  name: 'GlobalMenu',
  data(){
    return{
      showMenu: false,
    }
  },
  // wrap-router表示中にbodyにクラスを追加するwatch
  watch:{
    showMenu:{
      handler(){
        if(this.showMenu){
          document.body.className = 'lock';
        } else {
          document.body.className = '';
        }
      }
    }
  },
  components:{
    HamburgerIcon
  }
}
</script>

<style scoped>
.global-nav {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 7vh;
  margin-bottom: 5vh;
  z-index: 4000;
  
  /* background-color: rgba(255,0,0,.5); */
}

.hamburger{
  position: absolute;
  border: transparent;
  background: transparent;
  width: 35px;
  height: 35px;
  padding: 0;
  z-index: 4002;
}

.wrap-router{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  height: auto;
  margin-left: 29vw;
  z-index: 4001;
}


.wrap-router a {
  color: #dce3eb;
  text-decoration: none;
  font-size: 1.3vw;
}

.wrap-router a.router-link-exact-active {
  color: #16b8f3;
}

/* 画面幅1200pxの設定 */
@media screen and (max-width:1200px){
  .global-nav{
    position: fixed;
  }
  .wrap-router{
    justify-content: center;
    width: auto;
  }
  .global-nav a{
    width: 20%;
    font-size: 2vw;
  }
}

/* 画面幅600pxの設定 */
@media screen and (max-width:600px){
  .global-nav{
    left: 0;
    margin: 0;
  }

  .hamburger{
    margin: 1.5rem 0 0 1.5rem;
  }

  .wrap-router a{
    width: 100%;
    font-size: 6vw;
  }

  .wrap-router{
    position: fixed;
    width: 100vw;
    height: 100vh;
    margin: 0;
    bottom: 0;
    background-color: rgba(6,6,6,1);
    flex-flow: column;
    justify-content: space-around;
  }
}

</style>
